/* size variables */
$watch-size: 400px;
$arr-min-height: ($watch-size * 0.5) * 0.6;
$arr-sec-height: ($watch-size * 0.5) * 0.9;
$watch-bolt-size: 12px;
$arr-width: 5px;

/* color variables */
$color-background: #e0e0e0;
$color-dark-blue: rgb(12, 57, 124);

/* rotate variables */
$arr-rotate-degree: 6deg; /* (360deg / 60sec) */
